import React from "react";

function Writing__featured_large() {
	return (
		<div className="Writing__fl">
			<h3 className="Writing__fl-heading">Heading</h3>
			<p className="Writing__fl-content">
				डोरेमॉन हे खरं तर एक कार्टून आहे, लहान मुलांना हसवायला आणि त्यांचं
				मनोरंजन करायला ते बनवलेलं आहे, त्यामुळे त्याला फार गांभीर्यानं घ्यायची
				आणि त्या कार्टूनचा संबंध एका मर्यादेपलिकडे वास्तवाशी जोडायची गरज नाही,
				पण या एका भागात मात्र सुखवस्तू घरात लहानाचं मोठं होणाऱ्या मुलांच्या मनात
				पैसे साठवण्याविषयीची उदासीनता फार मजेशीर रीतीनं मांडलेली आहे असं मला
				वाटलं. अर्थात, नोबिताच्या पात्राचं वय लहान आहे आणि त्या वयात तो
				अर्थसाक्षर असेल अशी अपेक्षा ठेवणं वेडेपणाचं आहे, पण उघड्या डोळ्यांनी
				आजूबाजूला पाहिलं तर सुखवस्तू घरातली बरीच मुलं-मुली मोठी झाली तरी आर्थिक
				व्यवहारांच्या बाबतीत नोबितापेक्षा फार मोठी झालेली नसतात असंच मला वाटतं.
				फार लांब कशाला, माझ्या आर्थिक अनभिज्ञतेला मी स्वतः जवळून पाहिलेलं आहे,
				पण या अनभिज्ञतेचा दोष केवळ माझाच आहे असं मला वाटत नाही.
			</p>
			<button className="Writing__fl-readMore">Read Full</button>
		</div>
	);
}

export default Writing__featured_large;
