import React from "react";

import { Link, useNavigate } from "react-router-dom";

import "../assets/styles/menu/menu.css";
import close from "../assets/images/branding/close.svg";

function Menu() {
	const navigate = useNavigate();

	return (
		<div className="Menu">
			<div className="Menu__header">
				<img
					onClick={() => navigate(-1)}
					className="Menu__close-button"
					src={close}
					alt=""
				/>
			</div>
			<div className="Menu__links">
				<Link className="Menu__link" to="/work">
					<h1>Work</h1>
				</Link>
				<Link className="Menu__link" to="">
					<h1>Expressions</h1>
				</Link>
			</div>
			<div className="Menu__submenu">
				<Link to="/expressions/writing">
					<h2>Writing</h2>
				</Link>
				<h2>Photography</h2>
				<h2>Podcasts</h2>
				<h2>Videos</h2>
				<h2>Coding</h2>
			</div>
		</div>
	);
}

export default Menu;
