import React, { useEffect, useState } from "react";
import { http_insctance } from "../api/http_instance";
import { Outlet } from "react-router-dom";
import Header from "../layout/Header";

function Expression() {
	const [blog, setBlog] = useState([]);

	const data = async () => {
		try {
			const d = await http_insctance.get("blog");
			console.log(d.data);
			setBlog(d.data);
			return d;
		} catch (error) {
			console.log(error);
		}
	};

	useEffect(() => {
		data();
	}, []);

	return (
		<div>
			<Header />
			<Outlet />
		</div>
	);
}

export default Expression;
