import React from "react";

import { Link } from "react-router-dom";

import logo from "../../assets/images/branding/logo.svg";
import menu from "../../assets/images/branding/menu.svg";
import "../../assets/styles/header/navbar.css";

function Navbar() {
	return (
		<div className="Navbar">
			<div className="Navbar__logo">
				<Link to="/">
					<img className="Navbar__image" src={logo} alt="" />
				</Link>
			</div>
			{/* <div className="Navbar__menu">
				<Link to="/menu">
					<img className="Navbar__image" src={menu} alt="" />
				</Link>
			</div> */}
		</div>
	);
}

export default Navbar;
